import React, { useState } from 'react';
import { Box, Button, TextField, Typography, Container, Paper, Tabs, Tab } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const Auth = () => {
  const [isLogin, setIsLogin] = useState(true);
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [fullName, setFullName] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const url = isLogin ? 'http://localhost:8000/api/login' : 'http://localhost:8000/api/register';
      const headers = { 'Content-Type': 'application/json' };
      let body;

      if (isLogin) {
        // 登录请求
        const response = await fetch('http://localhost:8000/api/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
          body: new URLSearchParams({
            username: username,
            password: password
          })
        });

        if (!response.ok) {
          throw new Error('登录失败，请检查用户名和密码');
        }

        const data = await response.json();
        localStorage.setItem('token', data.access_token);
        navigate('/');
      } else {
        // 注册请求
        body = JSON.stringify({
          username,
          email,
          password,
          full_name: fullName
        });

        const response = await fetch(url, {
          method: 'POST',
          headers,
          body
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.detail || '注册失败');
        }

        const data = await response.json();
        localStorage.setItem('token', data.access_token);
        navigate('/');
      }
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Paper elevation={3} sx={{ mt: 8, p: 4 }}>
        <Typography component="h1" variant="h5" align="center">
          {isLogin ? '登录' : '注册'}
        </Typography>
        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }}>
          <Tabs
            value={isLogin ? 0 : 1}
            onChange={(e, newValue) => setIsLogin(newValue === 0)}
            centered
            sx={{ mb: 3 }}
          >
            <Tab label="登录" />
            <Tab label="注册" />
          </Tabs>

          {!isLogin && (
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="邮箱"
              name="email"
              autoComplete="email"
              autoFocus
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          )}

          <TextField
            margin="normal"
            required
            fullWidth
            id="username"
            label="用户名"
            name="username"
            autoComplete="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />

          {!isLogin && (
            <TextField
              margin="normal"
              fullWidth
              id="fullName"
              label="全名"
              name="fullName"
              value={fullName}
              onChange={(e) => setFullName(e.target.value)}
            />
          )}

          <TextField
            margin="normal"
            required
            fullWidth
            name="password"
            label="密码"
            type="password"
            id="password"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />

          {error && (
            <Typography color="error" variant="body2" sx={{ mt: 2 }}>
              {error}
            </Typography>
          )}

          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
            disabled={loading}
          >
            {loading ? '处理中...' : isLogin ? '登录' : '注册'}
          </Button>
        </Box>
      </Paper>
    </Container>
  );
};

export default Auth;